.create-question-btn {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 10px 30px;
}
.create-card {
  margin-bottom: 25px;
}

.card-title {
  font-size: 1.5em;
}

.btn-cancel {
  margin-left: 1em;
}
.question-card {
  margin-bottom: 25px;
}

.card-footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.card-actions {
  display: flex;
  align-items: center;
}

.votes {
  margin: 0 10px;
  font-size: 1.5em;
}

.btn-delete {
  margin-left: 25px;
}

.btn-up, .btn-down {
  -webkit-transform: scale(2);
          transform: scale(2);
}

.btn-up, .btn-down, .btn-delete{
  opacity: 0.2;
}

.btn-up:hover, .btn-down:hover, .btn-delete:hover {
  opacity: 1;
}




.question-list {
  margin-top: 25px;
}
.app {
  margin: 100px auto;
  width: 80%;
  max-width: 800px;
}
body {
  font-family: 'Noto Serif', serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 10px;
}

#root {
  font-size: 1.6em;
}

